import React from "react";
import '@wcj/dark-mode';
import { css, cx } from '@emotion/css'
import styled from '@emotion/styled'
import { Button, P } from "./StyleComponent"
import Buttons from "./StyleComponent/Button"

let SomeComp = styled.div({
  color: 'hotpink'
})

let AnotherComp = styled.div`
  color: ${props => props.color};
`

function App(props: any) {
  const [count, setCount] = React.useState(1)
  const color = 'darkgreen'
  const cals = css`
    background-color: var(--color-header-bg,hotpink);
    &:hover {
      color: ${color};
    }
  `

  const marginTop = css`
    margin-top: 20px;
  `

  const appStyle = css`
  width:920px;
  margin:0 auto;
  `
  const styles = css(props.style)

  return (
    <div className={appStyle}>
      <dark-mode permanent></dark-mode>
      <div className={cx(cals, marginTop, styles, "one")} >
        {count}
      </div>
      <div className="dot" >
        {count}
      </div>
      <SomeComp>
        <AnotherComp color="green" />
      </SomeComp>
      <Button className={marginTop} onClick={() => setCount(count + 1)} >styled-components </Button>
      <P>styled-components p</P>
      <Buttons style={{ color: "red" }} >button</Buttons>
      <Buttons size="large"  >button</Buttons>
      <Buttons types="primary" >button</Buttons>
      <Buttons disabled >button</Buttons>
      <Buttons types="danger" >button</Buttons>
      <Buttons types="success" >button</Buttons>
      <Buttons block >button</Buttons>
      <Buttons active >button</Buttons>
      <Buttons basic >button</Buttons>
      <Buttons focus >button</Buttons>
    </div>
  );
}

export default App;
